			<categorymask ref="categorychild" @categorydata="categorydata"></categorymask>
			<!--   -->
			<productmask ref="productchild" @appenddata="appenddata"></productmask>
			<!--   -->
			<teststandardmask ref="standardchild" @testbasis="addbasis"></teststandardmask>
			<!--   -->
			<testprojectmask ref="projectchild" @testproject="addproject"></testprojectmask>
		</div>
	</div>
</template>

<script>
	import Config from '../../config.js'
	import inspectmask from '../common/common_mask/inspectmask.vue'//
	import sampletmask from '../common/common_mask/samplemask.vue'//
	import templatemask from '../common/common_mask/templatemask.vue'//
	import approvalmask from '../workflow/approving.vue'
	import flowhistorymask from '../workflow/flowhistory.vue'
	import flowmapmask from '../workflow/flowmap.vue'
	import vewPoplemask from '../workflow/vewPople.vue'
	import { Loading } from 'element-ui'
	import categorymask from '../common/common_mask/categorylistmask.vue'//
	import productmask from '../common/common_mask/productlistmask.vue'//
	import teststandardmask from '../common/common_mask/teststandardmask.vue'//
	import testprojectmask from '../common/common_mask/testprojectmask.vue'//
	export default {
		name: 'masks',
		components: {
			 approvalmask,
			 flowhistorymask,
			 flowmapmask,
			 vewPoplemask,
			 inspectmask,
			 sampletmask,
			 templatemask,
			 categorymask,
			 productmask,
			 teststandardmask,
			 testprojectmask
		},
		data() {
			var validateProxynum = (rule, value, callback) => {//
                if (this.workorderForm.PROXYNUM === undefined || this.workorderForm.PROXYNUM === '' || this.workorderForm.PROXYNUM === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateProxyversion = (rule, value, callback) => {//
                if (this.workorderForm.PROXY_VERSION === undefined || this.workorderForm.PROXY_VERSION === '' || this.workorderForm.PROXY_VERSION === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateItemname = (rule, value, callback) => {//
                if (this.workorderForm.ITEM_NAME === undefined || this.workorderForm.ITEM_NAME === '' || this.workorderForm.ITEM_NAME === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateItemname = (rule, value, callback) => {//
                if (this.workorderForm.ITEM_MODEL === undefined || this.workorderForm.ITEM_MODEL === '' || this.workorderForm.ITEM_MODEL === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateItemnum = (rule, value, callback) => {//
                if (this.workorderForm.ITEMNUM === undefined || this.workorderForm.ITEMNUM === '' || this.workorderForm.ITEMNUM === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			return {
				approvingData:{},//
				file_url: Config.file_url,
				dialogVisible2:false,
				workorderForm: {
					WORKORDER_BASISList:[],//
					WORKORDER_PROJECTList:[],//
					WORKORDER_CHECKPERSONList:[],//
					WORKORDER_DATA_TEMPLATEList:[],//
					WORKORDER_REPORT_TEMPLATEList:[],//
					WORKORDER_ASSETList:[],//
					WORKORDER_REPORT_TEMPLATEList:[],//
					WORKORDER_CONTRACTList:[],//
				},
				basic_url: Config.dev_url,
				loadSign:true,//
				commentArr:{},
				selMenu:[],
				show: false,
				modify:false,
				isok1: true,
				isok2: false,
				down: true,
				up: false,
				addtitle:true,//titile
				modifytitle:false,//titile
				viewtitle: false, //title
				views: false,
				edit: true, //
				noedit:false,
				approval:false,
				start:false,
				activeName: 'first', //tabs
				activeNames: ['1','2','3','4','5','6','7'],//
				labelPosition: 'right', //
				// searchList: { //
				// 	WONUM: '',//
				// 	ITEM_NAME: '',//
				// 	PROXYNUM: '',//
				// 	STATE: '',//
				// 	COMPLETE_DATE: '',//
				// 	ENTERBY: '',//
				// 	ENTERDATE: '',//
				// },
				search:'',
				selectData:[],//
				Select_ITEM_STATUS:[],//-
				Select_ITEM_SOURCE:[],//-
				Select_COMPLETE_MODE:[],//-
				Select_ITEM_RECEPT_STATUS:[],//-
				Select_ITEM_CHECK_STATUS:[],//-
				Select_ITEM_MANAGEMENT:[],//-
				fileList:[],//
				rules: {
					PROXYNUM: [{ required: true, validator: validateProxynum}],//
					PROXY_VERSION: [{ required: true, validator: validateProxyversion}],//
					WONUM: [{ required: true, message: '', trigger: 'blur' }],
					ITEM_NAME: [{ required: true,validator: validateItemname}],//
					ITEM_MODEL: [{ required: true,validator: validateItemname}],//
					ITEMNUM: [{ required: true,validator: validateItemnum}],//
					ITEM_STATU: [{ required: true, message: '', trigger: 'blur' }],
					ITEM_STATUS: [{ required: true, message: '', trigger: 'blur' }],
					ITEM_SOURCE: [{ required: true, message: '', trigger: 'change' }],
					ITEM_QUALITY: [{ required: true, message: '', trigger: 'blur' }],
					CHECK_BASIS: [{ required: true, message: '', trigger: 'blur' }],
					COMPLETE_DATE: [{ required: true, message: '', trigger: 'blur' }],
					COMPLETE_MODE: [{ required: true, message: '', trigger: 'blur' }],
					ITEM_RECEPT_STATUS: [{ required: true, message: '', trigger: 'blur' }],
					ITEM_PROFESSIONAL_GROUP: [{ required: true, message: '', trigger: 'blur' }],
					STATUS: [{ required: true, message: '', trigger: 'blur' }],
				},
				//tree
				resourceData: [], //
				isEditList: false,  //
				selval:[],
				userList:[],
				samplesList:[],
				categoryList:[],
				numtips:'',
				workorder:'workorder',//appname
				page: {
					currentPage: 1,
					pageSize: 20,
					totalCount: 0
				},
				isEditing: true,
				modulenum:'',
				username:'',
				maingroup:[],//
				docParm: {}
			};
		},
		methods: {
			downLoadRow(row){
				if(row.FILECHECKED){
					var url = row.FILEPATH 
                        + '&userid=' + this.docParm.userid
                        + '&username=' + this.docParm.username
                        + '&deptid=' + this.docParm.deptid
                        + '&deptfullname=' + this.docParm.deptfullname;
                	window.open(url);
				}else if(row.FILE_ORGCHECKED){
					var url = row.FILEPATH_ORG 
                        + '&userid=' + this.docParm.userid
                        + '&username=' + this.docParm.username
                        + '&deptid=' + this.docParm.deptid
                        + '&deptfullname=' + this.docParm.deptfullname;
                	window.open(url);
				}else{
					this.$message({
						message: '',
						type: 'error'
					});
				}
			},
			deleteTempRow(index,row){
				if(row.FILECHECKED){
					var url = this.file_url + '/file/deleteFile/' + row.FILEID;
					this.$axios.delete(url,{}).then((res) => {
						if(res.data.code == 1){
							this.$message({
								message: '',
								type: 'success'
							});
							row.FILEID = 0;
							row.FILECHECKED = false;
							row.FILEPATH = '';
							row.FILESIZE = 	'';
						}else{
							this.$message({
								message: '',
								type: 'error'
							});
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
				}else if(row.FILE_ORGCHECKED){
					this.$message({
						message: '',
						type: 'error'
					});
				}else{
					this.deleteRow(index, row, 'moduleList');
				}
			},
			upload(e){
				var list = this.workorderForm.WORKORDER_DATA_TEMPLATEList;
				var editData = {};
				var isEdit = false;
				for(var i=0; i<list.length; i++){
					if(list[i].isEditing){
						editData = list[i];
						isEdit = true;
					}
				}
				if(!isEdit){
					this.$message({
						message: '',
						type: 'error'
					});
					return;
				}
				var formData = new FormData();
				var loading;
				loading = Loading.service({
					fullscreen: true,
					text: '...',
					background: 'rgba(F,F, F, 0.8)'
				});
				formData.append('files', document.getElementById('excelFile').files[0]);
				var config = {
					//
					headers: { "Content-Type": "multipart/form-data" },
					//
					onUploadProgress: e => {
						var completeProgress = ((e.loaded / e.total * 100) | 0) + "%";
						this.progress = completeProgress;
					}
				};
				var url = '';
				var url = this.file_url + '/file/uploadfile?userid=' + this.docParm.userid 
						+ '&username=' + this.docParm.username
						+ '&deptid=' + this.docParm.deptid
						+ '&deptfullname=' + this.docParm.deptfullname
						+ '&recordid=' + this.docParm.recordid
						+ '&appname=' + this.docParm.appname
						+ '&appid=' + this.docParm.appid;
				console.log(url);
				this.$axios.post(url, formData, config
				).then((res)=>{
					loading.close();
					if(res.data.code == 0){
						this.$message({
							message: res.data.message,
							type: 'error'
						});
					}else{
						this.$message({
							message: '',
							type: 'success'
						});
						editData.FILEID = res.data.fileid;
						editData.FILESIZE = res.data.filesize;
						editData.FILEPATH = res.data.webUrl;
					}
				})
			},
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			reset(){
            	this.workorderForm = {
					PROXYNUM: '',//
					PROXY_VERSION: '',//
					PARENT_NUM: '',//
					IS_MAIN: '',//
					MASTER_INSPECTOR: '',//
					CJDW:'',//
					STATE: '',//
					STATUS: '',//
					WONUM: '',//
					ITEM_NAME: '',//
					ITEM_MODEL: '',//
					ITEM_TRADEMARK: '',//
					ITEMNUM: '',//
					CHECK_DATE: '',//
					PRODUCT_DATE: '',///
					ITEM_STATU: '',//
					ITEM_STATUS: '',//
					ARRIVAL_DATE: '',//
					ITEM_QUALITY: '',//
					ITEM_SOURCE: '',//
					CHECK_BASIS: '',///
					TECHNICAL_INFORMATION: '',//
					SUB_PROJECT: '',//
					SPECIAL_REQUIREMENTS: '',//
					ITEM_RECCEPT_USER: '',//
					ITEM_RECEPT_DATE: '',//
					COMPLETE_DATE: '',//
					COMPLETE_MODE: '',//
					ITEM_RECEPT_STATUS: '',//
					ITEM_PROFESSIONAL_GROUP: '',//()
					UNDERTAKE_DATE: '',//
					ITEM_RETURN_QUALITY: '',//
					RETURN_ITEM_USER: '',//
					RETURN_ITEM_DATE: '',//
					ITEM_CHECK_STATUS: '',//
					ITEM_MANAGEMENT: '',//
					ITEM_UNDERTAKE_USER: '',//
					PROFESSIONAL: '',///
					CHECK_BASIS: '',//
					SEND: '',//
					FILE: '',//
					SEND_DATE: '',//
					FILE_DATE: '',//
					ENTERBY: '',//
					ENTERDATE: '',//
					ORG_CODE: '',//
					CHANGEBY: '',//
					CHANGEDATE: '',//
					STATEDesc:'',
					STATE:'1',
					WORKORDER_BASISList:[],//
					WORKORDER_PROJECTList:[],//
					WORKORDER_CHECKPERSONList:[],//
					WORKORDER_DATA_TEMPLATEList:[],//
					WORKORDER_ASSETList:[],//
					WORKORDER_REPORT_TEMPLATEList:[],//
					WORKORDER_CONTRACTList:[],//					
				};
			},
			//
			childByValue: function (childValue) {
		        // childValue
		        this.selMenu = childValue
		    },
			iconOperation(row, column, cell, event) {
				if(column.property === "iconOperation") {
					row.isEditing = !row.isEditing;
				}
			},
			handleClick(tab, event) {
//		        console.log(tab, event);
		   },
			sizeChange(val) {
				this.page.pageSize = val;
				this.getuser();
			},
			currentChange(val) {
				this.page.currentPage = val;
				this.getuser();
			},
			//
			addworkorder(){	
				this.$refs.inspectchild.visible();
			},
			appendpro(value){
				this.workorderForm.PROXYNUM =value;
					
			},
			appendver(value){
				this.workorderForm.PROXY_VERSION =value;
			},
			//
			addsample(type){
				this.$refs.samplechild.visible(type);
			},
			appendname(value){
				this.workorderForm.ITEM_NAME = value;//
			},
			appendmod(value){
				this.workorderForm.ITEM_MODEL = value;//
			},
			appendsta(value){
				this.workorderForm.ITEM_STATUS = value;//
			},
			appendite(value){
				this.workorderForm.ITEMNUM = value;//
			},
			addperson(num){
				this.getuser();
				this.numtips = num;
				this.dialogVisible2 = true;
			},
			addpersonname(){
				if(this.selMenu.length == 0){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else if(this.selMenu.length > 1){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					if(this.numtips == '1'){
						this.workorderForm.MASTER_INSPECTOR = this.selMenu[0].nickname;
					}else if(this.numtips == '2'){
						this.workorderForm.ITEM_PROFESSIONAL_GROUP = this.selMenu[0].nickname;
					}else if(this.numtips == '3'){
						this.workorderForm.RETURN_ITEM_USER = this.selMenu[0].nickname;
					}else if(this.numtips == 'sampleget'){
						this.workorderForm.ITEM_UNDERTAKE_USER = this.selMenu[0].nickname;
					}else if(this.numtips == 'qualityperson'){
						this.workorderForm.PROFESSIONAL = this.selMenu[0].nickname;
					}
					this.dialogVisible2 = false;
					this.getuser();
				}
			},
			getCompany() {
				var type = "2";
				var url = this.basic_url + '/api-user/depts/treeByType';
				this.$axios.get(url, {
					params: {
						type: type
					},
				}).then((res) => {
					this.selectData = res.data;
				});
			},
			//()
			RVENDORSelect(RVENDORid){
				var url = this.basic_url + '/api-user/depts/findByPid/'+RVENDORid;
					this.$axios.get(url, {}).then((res) => {
						this.maingroup = res.data;
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
				this.workorderForm.PRODUCT_TYPE = '';
				this.workorderForm.P_NUM = '';
				this.workorderForm.PRODUCT = '';
				this.workorderForm.PRO_NUM = '';
				this.workorderForm.S_NUM = '';
				this.workorderForm.WORKORDER_BASISList = [];
				this.workorderForm.WORKORDER_PROJECTList    = [];
				ITEM_PROFESSIONAL_GROUP:'';//
			},
			//
			addcategory(){
				if(this.workorderForm.CJDW == null || this.workorderForm.CJDW == '' || this.workorderForm.CJDW == undefined){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.$refs.categorychild.visible(this.workorderForm.CJDW);
					
				}
			},
			//
			categorydata(value){
				this.workorderForm.P_NUM = value[0];
				this.workorderForm.PRODUCT_TYPE  = value[1];
				this.workorderForm.PRODUCT = '';
				this.workorderForm.PRO_NUM = '';
				this.workorderForm.S_NUM = '';
				this.workorderForm.WORKORDER_BASISList = [];
				this.workorderForm.WORKORDER_PROJECTList = [];
			},
			addproduct(){//
				if(this.workorderForm.P_NUM == null || this.workorderForm.P_NUM == '' || this.workorderForm.P_NUM == undefined){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.$refs.productchild.visible(this.workorderForm.P_NUM);
				}
			},
			//
			appenddata(value){
				this.workorderForm.PRO_NUM = value[0];
				this.workorderForm.PRODUCT = value[1];
				this.workorderForm.S_NUM = '';
				this.workorderForm.WORKORDER_BASISList = [];
				this.workorderForm.WORKORDER_PROJECTList = [];
			},
			//
			basisleadbtn(){
				if(this.workorderForm.PRO_NUM == null || this.workorderForm.PRO_NUM == '' || this.workorderForm.PRO_NUM == undefined){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.$refs.standardchild.basislead(this.workorderForm.PRO_NUM);
				}
			},
			//
			addbasis(value){
				this.workorderForm.S_NUM = value[0];
				for(var i = 1;i<value.length;i++){
					value[i].S_DESC = value[i].S_NAME;
					this.workorderForm.WORKORDER_BASISList.push(value[i]);
				}
				this.workorderForm.WORKORDER_PROJECTList = [];
			},
			//
			basisleadbtn2(){
				if(this.workorderForm.S_NUM == null || this.workorderForm.S_NUM == '' || this.workorderForm.S_NUM == undefined){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.$refs.projectchild.projectlead(this.workorderForm.S_NUM);
				}
			},
			 //
			addproject(value){
				for(var i = 0;i<value.length;i++){
					value[i].P_DESC = value[i].P_NAME;
					this.workorderForm.WORKORDER_PROJECTList.push(value[i]);
				}
			},
			 //
            templateNumber(item){
            	this.modulenum = item;
            	this.$refs.templatechild.visible();
            },
			showModule(data){
				this.modulenum.D_NUM = data.num;
				this.modulenum.D_DESC = data.desc;
				var url = this.file_url + '/file/fileList?page=0&size=10';
				this.$axios.post(url,{
					'appname': '_',
					'recordid': data.id,
				}).then((res) => {
					this.modulenum.FILESIZE_ORG = res.data.fileList[0].filesize;
					this.modulenum.FILEPATH_ORG = res.data.fileList[0].filepath;
					this.modulenum.FILEID_ORG = res.data.fileList[0].fileid;
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},
   			//-
			getITEM_STATUS() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=ITEM_STATUS';
				this.$axios.get(url, {}).then((res) => {
					this.Select_ITEM_STATUS = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//-
			getITEM_SOURCE() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=ITEM_SOURCE';
				this.$axios.get(url, {}).then((res) => {
					this.Select_ITEM_SOURCE = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//-
			getCOMPLETE_MODE() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=COMPLETE_MODE';
				this.$axios.get(url, {}).then((res) => {
					this.Select_COMPLETE_MODE = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//-
			getITEM_RECEPT_STATUS() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=ITEM_RECEPT_STATUS';
				this.$axios.get(url, {}).then((res) => {
					this.Select_ITEM_RECEPT_STATUS = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//-
			getselectData() {
				var page = this.page.currentPage;
				var limit = this.page.pageSize;
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=DEPT_TYPE';
				this.$axios.get(url, {}).then((res) => {
					this.selectData = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//-
			getITEM_CHECK_STATUS() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=ITEM_CHECK_STATUS';
				this.$axios.get(url, {}).then((res) => {
					this.Select_ITEM_CHECK_STATUS = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//-
			getITEM_MANAGEMENT() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=ITEM_MANAGEMENT';
				this.$axios.get(url, {}).then((res) => {
					this.Select_ITEM_MANAGEMENT = res.data;
				}).catch(error => {
					console.log('');
				})
			},
           
			//
			handleChangeQuality(value) {
				console.log(value);
			},
			// End
   			judge(data) {
				return data.STATUS ? '' : ''
			},
   			
            //
			deleteRow(index, row, listName){
				console.log(row);
				var TableName = '';
				console.log(listName);
				if(listName =='basisList'){
					TableName = 'WORKORDER_BASIS';
				}else if(listName =='projectList'){
					TableName = 'WORKORDER_PROJECT';
				}else if(listName =='peopletList'){
					TableName = 'WORKORDER_CHECKPERSON';
				}else if(listName =='moduleList'){
					TableName = 'WORKORDER_DATA_TEMPLATE';
				}else{
					TableName = 'WORKORDER_ASSET';
				}
				if(row.ID){
					var url = this.basic_url + '/api-apps/app/workorder/' + TableName +'/' + row.ID;
					this.$axios.delete(url, {}).then((res) => {
						if(res.data.resp_code == 0){
							this.workorderForm[TableName+'List'].splice(index,1);
							this.$message({
								message: '',
								type: 'success'
							});
						}else{
							this.$message({
								message: res.data.resp_msg,
								type: 'error'
							});
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
				}else{
					this.workorderForm[TableName+'List'].splice(index,1);
				}
			},
			//
			proagree(row){
				console.log(row);
				var data = {
					"WORKORDER_CONTRACTID":row.ID.toString(),
				};
				var url = "http://192.168.1.115:7902/app/workorder/operate/subproject";
				this.$axios.post(url,data).then((res) => {
					console.log(res);
					if(res.data.resp_code == 0) {
						this.$message({
							message: '',
							type: 'success'
						});
					}
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},
			//
			startup(){
				var url = this.basic_url + '/api-apps/app/workorder/flow/'+this.dataid;
				this.$axios.get(url, {}).then((res) => {
					console.log(res);
					if(res.data.resp_code == 1) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
				    }else{
				    	this.$message({
								message:res.data.resp_msg,
								type: 'success'
							});
							this.detailgetData();
							var url = this.basic_url + '/api-apps/app/workorder/flow/Executors/'+this.dataid;
							this.$axios.get(url, {}).then((res) => {
								var resullt=res.data.datas;
								var users='';
								for(var i=0;i<resullt.length;i++){
									users = users + resullt[i].username+",";
								}
								if(users.indexOf(this.username) != -1){
									this.approval=true;
									this.start=false;
								}else{
									this.approval=false;
									this.start=false;
								}
							});
				    }
				});
			},
			//
			approvals(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workorder;
				 var url = this.basic_url + '/api-apps/app/workorder/flow/isEnd/'+this.dataid;
		    		this.$axios.get(url, {}).then((res) => {
		    			if(res.data.resp_code == 0) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
		    			}else{
		    				var url = this.basic_url + '/api-apps/app/workorder/flow/isExecute/'+this.dataid;
		    				this.$axios.get(url, {}).then((res) => {
				    			if(res.data.resp_code == 1) {
										this.$message({
											message:res.data.resp_msg,
											type: 'warning'
										});
								}else{
									var url = this.basic_url + '/api-apps/app/workorder/flow/customFlowValidate/'+this.dataid;
								this.$axios.get(url, {}).then((res) => {
				    				if(res.data.resp_code == 0) {
										this.$message({
											message:res.data.resp_msg,
											type: 'warning'
										});
									}else{
									 	this.$refs.approvalChild.visible();
									}
								})
								}
		    				});
		    			}
					});
			},
			//
			flowhistory(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workorder;
//				this.$refs.flowhistoryChild.open();
				this.$refs.flowhistoryChild.getdata(this.dataid);
			},
			//
			flowmap(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workorder;
				this.$refs.flowmapChild.getimage();
			},
			//
			viewpepole(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.workorder;
				this.$refs.vewPopleChild.getvewPople(this.dataid);
			},
			//
			SelChange(val) {
				this.selMenu = val;
			},
			
			addfield1(){//
            	var date=new Date();
				this.currentDate = this.$moment(date).format("YYYY-MM-DD  HH:mm:ss");
				var index=this.$moment(date).format("YYYYMMDDHHmmss");
				var obj = {
					S_NUM: '',
					WP_LINENUM: index,
					ITEM_NAME: '',
					MODEL: '',
					V_NAME: '',
					BASIS: '',
					P_NAME: '',
					CHECKCOST: '',
					REASION: '',
					MEMO: '',
					isEditing: true,
				};
				console.log(typeof(this.workorderForm.WORKORDER_BASISList));
				this.workorderForm.WORKORDER_BASISList.push(obj);//
			},
			addfield2() {
				var obj = {
					P_NUM: '',
					P_DESC: '',
					// HOSTPERSON: '',
					// FOLLOWPERSON: '',
					REMARKS:'',
					VERSION:'',
					isEditing: true,
				};
				this.workorderForm.WORKORDER_PROJECTList.push(obj);
			},
			//
			addfield3(){
				var obj = {
					NAME:'',
					USER:'',
					ISMAINPERSON:'',
					TELPHONE: '',
					DEPARTMENT: '',
					isEditing: true,
				};
				this.workorderForm.WORKORDER_CHECKPERSONList.push(obj);
			},
			//
			addfield4(){
				var obj = {
					D_NUM:'',
					D_DESC: '',
					STATUS: '1',
					isEditing: true,
					FILEID: '',
					FILEPATH: '',
					FILESIZE: '',
					FILEID_ORG: '',
					FILEPATH_ORG: '',
					FILESIZE_ORG: '',
					FILECHECKED: false,
					FILE_ORGCHECKED: false,
				};
				this.workorderForm.WORKORDER_DATA_TEMPLATEList.push(obj);
			},
			//
			addequip(){
				var obj = {
					ID:'',
					ASSETNUM:'',
					WONUM:'',
					DESCRIPTION:'',
					MODEL:'',
					USE_PERSON:'',		
					isEditing: true,	
				};
				this.workorderForm.WORKORDER_ASSETList.push(obj);
			},
			//
			getreport(){
				var changeUser = this.workorderForm.WORKORDER_DATA_TEMPLATEList;
				//basisnum
				var id = [];
				for (var i = 0; i < changeUser.length; i++) {
					id.push(changeUser[i].FILEID);		
				}
				//basisnumsbasisnum
				var ids = id.toString(',');
				debugger;
				var url = this.basic_url +"/api-merge/merge/workorder/MergeWord?filePath="+ids+"&fileName=&num="+this.workorderForm.PROXYNUM+"&deptfullname="+this.workorderForm.DEPTIDDesc+"&recordid="+this.workorderForm.ID;
				this.$axios.post(url, {}).then((res) => {
					console.log(res);
					if(res.data.resp_code == 0) {
						this.$message({
							message: '',
							type: 'success'
						});
					}
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},
			//
			visible() {
				var date = new Date();
				this.workorderForm.ENTERDATE = this.$moment(date).format("YYYY-MM-DD");
				this.$axios.get(this.basic_url + '/api-user/users/currentMap', {}).then((res) => {
					this.workorderForm.DEPTID = res.data.deptId;
					this.workorderForm.ENTERBY = res.data.id;
					this.username=res.data.username;
					var date = new Date();
					this.workorderForm.ENTERDATE = this.$moment(date).format("YYYY-MM-DD");
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
				this.reset();
				this.views = false
				this.modify = false;
				this.show = true;
				this.addtitle = true;
				this.modifytitle = false;
				this.viewtitle=false;
				this.edit = true;
				this.noedit = false;
				
			},
			detailgetData() {
			var url = this.basic_url +'/api-apps/app/workorder/' + this.dataid;
				this.$axios.get(url, {}).then((res) => {
					console.log(res.data);
					//
					for(var i = 0;i<res.data.WORKORDER_BASISList.length;i++){
						res.data.WORKORDER_BASISList[i].isEditing = false;
					}
					//
					for(var i = 0;i<res.data.WORKORDER_PROJECTList.length;i++){
						res.data.WORKORDER_PROJECTList[i].isEditing = false;
					}
					//
					for(var i = 0;i<res.data.WORKORDER_CHECKPERSONList.length;i++){
						res.data.WORKORDER_CHECKPERSONList[i].isEditing = false;
					}
					//
					for(var i = 0;i<res.data.WORKORDER_DATA_TEMPLATEList.length;i++){
						res.data.WORKORDER_DATA_TEMPLATEList[i].FILECHECKED = false;
						res.data.WORKORDER_DATA_TEMPLATEList[i].FILE_ORGCHECKED = false;
						res.data.WORKORDER_DATA_TEMPLATEList[i].isEditing = false;
					}
					res.data.CJDW = Number(res.data.CJDW);
					res.data.ITEM_PROFESSIONAL_GROUP = Number(res.data.ITEM_PROFESSIONAL_GROUP);
					this.RVENDORSelect(res.data.CJDW);
					this.workorderForm = res.data;
					this.show = true;
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},	
			// 
			detail(dataid) {
				this.dataid=dataid;
				this.$axios.get(this.basic_url + '/api-user/users/currentMap', {}).then((res) => {
	    			this.workorderForm.DEPTID = res.data.deptId;//id
					this.workorderForm.CHANGEBY = res.data.id;
					var date = new Date();
					this.workorderForm.CHANGEDATE = this.$moment(date).format("yyyy-MM-dd");
					
					this.docParm.recordid = dataid;
					this.docParm.appname = '_';
					this.docParm.appid = '39';

					this.docParm.userid = res.data.id;
					this.docParm.username = res.data.username;
					this.docParm.deptid = res.data.deptId;
					this.docParm.deptfullname = res.data.deptName;
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error',
					});
				});
				this.detailgetData();
				this.views = false;
				this.addtitle = false;
				this.modifytitle = true;
				this.viewtitle=false;
				this.modify = true;
				this.show = true;
				this.edit = true;
				this.noedit = false;
			},
			//
			view(dataid) {
				// console.log(this.username);
				this.dataid=dataid;	
				this.modifytitle = false;
				this.addtitle = false;
				this.viewtitle = true;
				this.views = true; //
				this.noviews = false;
				this.edit = true;
				this.noedit = true;
				//
				this.detailgetData();
				var url = this.basic_url + '/api-apps/app/workorder/flow/isStart/'+dataid;
				this.$axios.get(url, {}).then((res) => {
					// console.log(res);
					if(res.data.resp_code==1){
						this.start=true;
						this.approval=false;
					}else{
						var url = this.basic_url + '/api-apps/app/workorder/flow/Executors/'+dataid;
						console.log(url);
						this.$axios.get(url, {}).then((res) => {
							// console.log(res.data.datas);
							res.data.CJDW = Number(res.data.CJDW);
							var resullt=res.data.datas;
							var users='';
							for(var i=0;i<resullt.length;i++){
								users = users + resullt[i].username+",";
								// console.log("users----"+users);
							}
							if(users.indexOf(this.username) != -1){
								this.approval=true;
								this.start=false;
							}
						});
					}
				});
			},
			// users/saveOrUpdate
			submitForm() {
				this.$refs.workorderForm.validate((valid) => {
		          if (valid) {
					var url = this.basic_url + '/api-apps/app/workorder/saveOrUpdate';
					// console.log(this.workorderForm);
					this.$axios.post(url,this.workorderForm).then((res) => {
						// console.log(res);
						//resp_code == 0
						if(res.data.resp_code == 0) {
							this.$message({
								message: '',
								type: 'success'
							});
							this.show = false;
							//
							this.$emit('requests');
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
			          } else {
			            return false;
			          }
			        });
			},
			
			//
			close() {
				this.show = false;
				this.$emit('request');
				//this.resetNew();
			},
			toggle(e) {
				if(this.isok1 == true) {
					this.maxDialog();
				} else {
					this.rebackDialog();
				}
			},
			maxDialog(e) { //60px
				this.isok1 = false;
				this.isok2 = true;
				$(".mask_div").width(document.body.clientWidth);
				$(".mask_div").height(document.body.clientHeight - 60);
				$(".mask_div").css("top", "60px");
			},
			//
			rebackDialog() { //
				this.isok1 = true;
				this.isok2 = false;
				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			
			//  
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
			},
			handleClose(done) {
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			loadMore (item) {
			    if (this.loadSign) {
			      this.loadSign = false;
			      this.page.currentPage++
			      if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			        return
			    	}
					setTimeout(() => {
					this.loadSign = true;
					}, 1000)
					this.getuser();
			    }
			},	
			
			getuser(){
				var data = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
				}
				 //
				this.$axios.get(this.basic_url + '/api-user/users?deptId='+this.workorderForm.CJDW, {
					params: data
				}).then((res) => {
					console.log(res);
					//this.userList = res.data.data;
					this.page.totalCount = res.data.count;
					//
					let totalPage = Math.ceil(this.page.totalCount / this.page.pageSize)
					if(this.page.currentPage >= totalPage) {
						this.loadSign = false
					} else {
						this.loadSign = true
					}
					this.commentArr[this.page.currentPage] = res.data.data
					let newarr = []
					for(var i = 1; i <= totalPage; i++) {
						if(typeof(this.commentArr[i]) != 'undefined' && this.commentArr[i].length > 0) {
							for(var j = 0; j < this.commentArr[i].length; j++) {
								newarr.push(this.commentArr[i][j])
							}
						}
					}
                    
					this.userList = newarr;
				}).catch((wrong) => {})	
			},
			getUser(){//
	            var url = this.basic_url + '/api-user/users/currentMap';
	            this.$axios.get(url, {}).then((res) => {//
	                    this.username = res.data.username;
	            }).catch((err) => {
	                this.$message({
	                    message: '',
	                    type: 'error'
	                });
	            });
        	},
			
		},
		
		mounted() {
			this.getITEM_STATUS();//-
			this.getITEM_SOURCE();//-
			this.getCOMPLETE_MODE();//-
			this.getselectData();//-
			this.getITEM_RECEPT_STATUS();//-
			this.getITEM_CHECK_STATUS();//-
			this.getITEM_MANAGEMENT();//-
			this.getuser();//
			this.getCompany();
			this.getUser();
		},
	}
</script>

<style scoped>
	@import '../../assets/css/mask-modules.css';
	.ml60 {margin-left: 60px;}
	.a-upload input{
		position: absolute;
		font-size: 100px;
		right: 0;
		top: 0;
		opacity: 0;
		filter: alpha(opacity=0);
		cursor: pointer;
		width: 40px;
		cursor: pointer;
	}
	.upload-btn{
		color: #fff;
		background-color: #286090;
		border-radius: 4px;
		padding: 4px 10px;
		width: 85px;
		height: 34px;
		line-height: 28px;
		border: none;
		cursor: pointer;
	}

</style>



